.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control {
    width: 50%;
    display:flex;
    padding:1rem;
    input {
        width: 100%;
        appearance: none;
        background-color: transparent;
        &:focus{
            outline: none;
        }
    }
    p {padding: 0 1rem;}
    input[type=range]::-webkit-slider-thumb {
        appearance: none;
        width: 16px;
        height: 1rem;
    }
}
.track {
    background: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow:hidden;
  }
.animate-track {
    background: rgb(204,204,204);
    width:100%;
    height:100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}

.play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    width: 40%;
    svg {
      cursor: pointer;
    }
  }

@media screen and (max-width:768px) {
  .time-control {
      width:90%;
  }
  .play-control {
      width:60%;
  }
}